/* mask */
.sv-megabox-mask{
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display:none;
  zoom: 1;
}
.sv-megabox-preloader{
  width:200px;
  height:100px;
  z-index: 1000;
  position:fixed;
  background:url(img/ajax-loader.gif) 50% 50% no-repeat;
  display:none;
}
 /* mask (end) */

/* foundation */
.sv-megabox.default-style {
  width:340px;
  font: normal 14px/16px "Arial",sans-serif;
}
.sv-megabox .sv-megabox-link-close {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  padding: 2px;
  background: #FFF url(img/close.png) 50% 50% no-repeat;
  cursor: pointer;
}
.sv-megabox.default-style .sv-megabox-link-close {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 18px;
  width: 18px;
  background: url(img/close.png) 50% 50% no-repeat;
  cursor: pointer;
  border-radius: 0;
  padding: 0;}
.sv-megabox.default-style .sv-megabox-link-close.white {
  background: url(img/close_white.png) 50% 50% no-repeat;
}
.sv-megabox.default-style .sv-megabox-wrap {
  background:#fff;
  overflow:hidden;
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius:4px;
}
.sv-megabox.default-style .sv-megabox-header {
  font-size: 16px;
  color: #000;
  text-align: center;
  margin:0;
  background-color: #D5DBDD;
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
}
.sv-megabox.default-style .sv-megabox-header span {
  display:block;
  padding:10px 30px;
}
.sv-megabox.default-style .sv-megabox-header > span:after {
  content: '.';
height: 0;
clear: both;
display: block;
visibility: hidden;
font-size: 0;
}
.sv-megabox.default-style .sv-megabox-content {
  font-size: 16px;
  color: #353535;
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  padding:20px 35px;
}
.sv-megabox.default-style.footer-on .sv-megabox-content {
  padding:20px 35px
}
.sv-megabox.default-style.footer-on.auto-off .sv-megabox-content {
  padding:20px 35px
}
.sv-megabox.default-style .sv-megabox-footer {
  overflow:hidden;
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  padding:0px 35px 20px 35px;
}
.sv-megabox-hide,.sv-megabox-img-tmp{
  position:absolute;
  left:-9999px;
  top:-9999px;
  visibility:hidden;
}
/* foundation (end) */

.sv-megabox.default-style button.fr{
  float: right;
}
.sv-megabox.default-style button.fl{
  float: left;
}
.sv-megabox.default-style button.full{
  width:100%;
}
.sv-megabox.default-style button.sv-megabox-button-ok {
  border-radius: 4px;
  cursor: pointer;
  border: none;
  text-transform: uppercase;
  box-shadow:1px 1px 4px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow:1px 1px 4px rgba(0, 0, 0, 0.5);
  -moz-box-shadow:1px 1px 4px rgba(0, 0, 0, 0.5);
  text-shadow: 0px 1px 1px rgba( 255, 255, 255, 0.5 );
  color:#5B6063;background: rgb(251,249,255);
  background: -moz-linear-gradient(top,  rgba(251,249,255,1) 0%, rgba(213,219,221,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(251,249,255,1)), color-stop(100%,rgba(213,219,221,1)));
  background: -webkit-linear-gradient(top,  rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -o-linear-gradient(top,  rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -ms-linear-gradient(top,  rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: linear-gradient(to bottom,  rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
}
.sv-megabox.default-style button.sv-megabox-button-ok:hover {
  background: rgb(213,219,221);
  background: -moz-linear-gradient(top,  rgba(213,219,221,1) 0%, rgba(251,249,255,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(213,219,221,1)), color-stop(100%,rgba(251,249,255,1)));
  background: -webkit-linear-gradient(top,  rgba(213,219,221,1) 0%,rgba(251,249,255,1) 100%);
  background: -o-linear-gradient(top,  rgba(213,219,221,1) 0%,rgba(251,249,255,1) 100%);
  background: -ms-linear-gradient(top,  rgba(213,219,221,1) 0%,rgba(251,249,255,1) 100%);
  background: linear-gradient(to bottom,  rgba(213,219,221,1) 0%,rgba(251,249,255,1) 100%);
}
.sv-megabox.default-style button.sv-megabox-button-no {
  color:#fff;
  text-shadow: 0px 1px 1px rgba( 0, 0, 0, 0.5 );
  border-radius: 4px;
  border:1px solid #ccc;
  background: rgb(124,124,124);
  background: -moz-linear-gradient(top,  rgba(124,124,124,1) 0%, rgba(69,72,77,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(124,124,124,1)), color-stop(100%,rgba(69,72,77,1)));
  background: -webkit-linear-gradient(top,  rgba(124,124,124,1) 0%,rgba(69,72,77,1) 100%);
  background: -o-linear-gradient(top,  rgba(124,124,124,1) 0%,rgba(69,72,77,1) 100%);
  background: -ms-linear-gradient(top,  rgba(124,124,124,1) 0%,rgba(69,72,77,1) 100%);
  background: linear-gradient(to bottom,  rgba(124,124,124,1) 0%,rgba(69,72,77,1) 100%);
}
.sv-megabox.default-style button.sv-megabox-button-no:hover {
  color:#fff;
  background: rgb(69,72,77);
  background: -moz-linear-gradient(top,  rgba(69,72,77,1) 0%, rgba(124,124,124,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(69,72,77,1)), color-stop(100%,rgba(124,124,124,1)));
  background: -webkit-linear-gradient(top,  rgba(69,72,77,1) 0%,rgba(124,124,124,1) 100%);
  background: -o-linear-gradient(top,  rgba(69,72,77,1) 0%,rgba(124,124,124,1) 100%);
  background: -ms-linear-gradient(top,  rgba(69,72,77,1) 0%,rgba(124,124,124,1) 100%);
  background: linear-gradient(to bottom,  rgba(69,72,77,1) 0%,rgba(124,124,124,1) 100%);
}
/* buttons (end) */

/* auto */
.sv-megabox.auto-on.link-close-on.header-off .sv-megabox-link-close {
  right: 30px;
  top: 4px;
}
.sv-megabox.auto-on .sv-megabox-header {
  border-bottom:1px solid #ccc;
  box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow:  0px 1px 15px rgba(0, 0, 0, 0.25);
  -moz-box-shadow:  0px 1px 15px rgba(0, 0, 0, 0.25);
}
.sv-megabox.auto-on .sv-megabox-content{
  overflow:auto;
}
.sv-megabox.auto-on.footer-off .sv-megabox-content{
  padding: 25px 35px;
}
.sv-megabox.auto-on.header-off.footer-off .sv-megabox-content{
  padding: 25px 35px
}
.sv-megabox.auto-on .sv-megabox-content.header-off.footer-off {
  padding: 25px 35px;
}
.sv-megabox.auto-on.footer-on .sv-megabox-footer {
  padding: 10px 55px 10px 35px;
  border-top:1px solid #ccc;
  box-shadow: 0px -1px 15px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow:  0px -1px 15px rgba(0, 0, 0, 0.25);
  -moz-box-shadow:  0px -1px 15px rgba(0, 0, 0, 0.25);
}
/* auto (end) */

/* prompt */
#promt-otvet{
  margin-bottom:10px;
}
.sv-megabox.sv-prompt .sv-megabox-footer input[type="text"] {
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius:4px;
  width:100%;
  background:#fff;
  border:1px solid #d5d5d5;
  padding:5px 10px;
  font:normal 14px/18px "Arial", sans-serif;
  color:#353535;
}
.sv-megabox.sv-prompt .sv-megabox-footer input[type="text"].error {
  border:1px solid #ff0000;
}
.sv-megabox.sv-prompt .sv-megabox-footer .error-text {
  color: #F00;
  text-align: center;
  margin-bottom: 3px;
}
/* prompt (end) */

/* tooltip */
.sv-megabox.tooltip{
  margin-top: 15px;
}
.sv-megabox.tooltip .sv-megabox-tringle{
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 15px 10px;
  border-color: transparent transparent #E1E8EA transparent;
}
.sv-megabox.tooltip.left .sv-megabox-tringle{
  left: 10px;
  top: -15px;
}
.sv-megabox.tooltip.right .sv-megabox-tringle{
  right: 10px;
  top: -15px;
}
.sv-megabox.tooltip .sv-megabox-header{
  background-color: #E1E8EA;
}
.sv-megabox.tooltip .sv-megabox-wrap{
  box-shadow: 3px 3px 24px 2px rgba(0, 0, 0, 0.4);
  -moz-box-shadow:3px 3px 24px 2px rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 3px 3px 24px 2px rgba(0, 0, 0, 0.4);
}


/* image */
.sv-megabox.image{}
.sv-megabox.image .sv-megabox-header .sv-megabox-img-counter,
.sv-megabox.image .sv-megabox-footer .sv-megabox-img-counter{
  float: left;
  font-weight: bold;
  color: #74828F;
  font-size: 14px;
}
.sv-megabox.image .sv-megabox-footer .sv-megabox-img-counter{
  margin-bottom:10px;
}
.sv-megabox.image .sv-megabox-header .sv-megabox-img-counter,
.sv-megabox.image .sv-megabox-header .sv-megabox-img-title,
.sv-megabox.image .sv-megabox-img-desc{
  padding:0;
  display:inline-block;
}
.sv-megabox.default-style.footer-on.auto-off.image.default-style .sv-megabox-content,
.sv-megabox.image.default-style .sv-megabox-content{
  overflow:hidden;
  padding:15px;
}
.sv-megabox.image .sv-megabox-img-box{
  position:relative;
  margin: 0 auto;
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-prev,
.sv-megabox.image .sv-megabox-img-box .sv-megabox-next{
  position:absolute;
  top:0;
  width:50%;
  height:100%;
  opacity: 0;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  zoom: 1;
}
.sv-megabox.image .sv-megabox-img-box:hover .sv-megabox-prev,
.sv-megabox.image .sv-megabox-img-box:hover .sv-megabox-next{
  cursor:pointer;
  opacity: 1;
  filter: alpha(opacity = 100);
  -moz-opacity: 1;
  zoom: 1;
}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-prev{left:0;}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-next{right:0;}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-prev span,
.sv-megabox.image .sv-megabox-img-box .sv-megabox-next span{
  position:absolute;
  top:50%;
  margin-top:-15px;
  width:30px;
  height:30px;
  display:block;
}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-prev > span{
  left:10px;
  background:url(img/sprite.png) 0 -60px no-repeat;
}
.sv-megabox.image .sv-megabox-img-box .sv-megabox-next > span{
  right:10px;
  background:url(img/sprite.png) 0 -30px no-repeat;
}

.sv-megabox.image .sv-megabox-footer,
.sv-megabox.image.auto-on.footer-on .sv-megabox-footer {
  width: 100%;
  padding: 20px 20px;
  border-top:1px solid #ccc;
  box-shadow: 0px -1px 15px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow:  0px -1px 15px rgba(0, 0, 0, 0.25);
  -moz-box-shadow:  0px -1px 15px rgba(0, 0, 0, 0.25);
}
.sv-megabox.image .sv-megabox-img-box img{
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius:4px;
}
.sv-megabox.image .sv-megabox-link-close {
  width: 29px;
  height: 29px;
  top: -14px!important;
  right: -14px!important;
  border-radius: 50%;
  background: url(img/sprite.png) 0 0 no-repeat;
  padding: 5px;
}

/* move */
.sv-megabox.move .sv-megabox-header{
  cursor: move;
}
.sv-megabox.move .sv-megabox-header span {
  background: url(img/move.png) 100% 50% no-repeat;
  padding: 10px 50px 10px 30px;
}
/* move (end) */

/* confirm */
.sv-megabox.sv-confirm .sv-megabox-footer,.sv-megabox.sv-confirm .sv-megabox-content {
  text-align:center;
}
.sv-megabox.sv-confirm .sv-megabox-footer button{
  margin:0 10px;
}
/* end confirm (end) */

/* content */
.sv-megabox.default-style .req {
  color: #F00;
  padding:0 3px;
  font-size:16px;
}
.sv-megabox.default-style .sv-megabox-content a {
  text-decoration:underline
  }
.sv-megabox.default-style .sv-megabox-content.link-dark a {
  color:#000;
}
.sv-megabox.default-style .sv-megabox-content.link-light a {
  color:#fff;
}
.sv-megabox.default-style .sv-megabox-content p {
  margin:0 0 20px;
}
.sv-megabox.default-style .sv-megabox-content b {
  font-weight:bold;
}
.sv-megabox.default-style .sv-megabox-content .title-form{
  text-align: center;
  font-size: 16px;
  color: #353535;
  margin:0;
  padding:10px 30px;
  color:#464D4F;
}
.sv-megabox.default-style .sv-megabox-content .field {
  margin-bottom:20px;
}
.sv-megabox.default-style .sv-megabox-content .field .title-field{
  margin-bottom:10px;
}

/* info - style */
.sv-megabox.info .sv-megabox-header {
  background: #5692d5;
