.sv-megabox.success .sv-megabox-link-close {
  background: url(img/close_white.png) 50% 50% no-repeat;
}
/* success - style (end) */

/* rose - style */
.sv-megabox.rose .sv-megabox-header {
  color: #D52747;
  border-top: 3px solid #D52747;
  text-align: center;
  background-color: #FFF;
  line-height:1;
}
.sv-megabox.rose .sv-megabox-header span{
  padding: 30px 25px 10px 25px;
  font-size: 31px;
}
.sv-megabox.rose .sv-megabox-wrap{
  border:0;
}
.sv-megabox.rose .sv-megabox-header span i{
  font-size: 43px;
  font-style:normal;
}
.sv-megabox.rose .sv-megabox-content{
  font-size: 21px;
  color: #434343;
  line-height: 1.261;
  text-align: center;
}
.sv-megabox.rose button.sv-megabox-button-ok {
  background:#D52747;
  color:#fff;
  text-shadow:none;
}
.sv-megabox.rose button.sv-megabox-button-ok:hover {
  background:#D52747;
}
/* rose - style (end) */