.sv-check-box{
  display:inline-block;
  position:relative;
  width:20px;
  height:20px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border:1px solid #ccc;
  vertical-align:middle;
  background: #FBF9FF;
  background: -moz-linear-gradient(top, rgba(251,249,255,1) 0%, rgba(213,219,221,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#FBF9FF), color-stop(100%,#D5DBDD));
  background: -webkit-linear-gradient(top, #FBF9FF 0%,#D5DBDD 100%);
  background: -o-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -ms-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: linear-gradient(to bottom, #FBF9FF 0%,#D5DBDD 100%);
  /*filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbf9ff', endColorstr='#d5dbdd',GradientType=0 );*/
  margin-right:10px;
}
.sv-check-box.disabled{
  background: #D1D7D9;
}
.sv-check-box.sv-checked>span{
  background:url(img/check.png) 50% 50% no-repeat;
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0;
}
.sv-check-box input{
  display:inline-block;
  position:absolute;
  width:100%;
  height:100%;
  opacity: 0;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  zoom: 1;
  cursor:pointer;
  margin:0;
  padding:0;
  top: 0;
  left: 0;
  z-index:1;
}

.sv-radio-box{
  display:inline-block;
  position:relative;
  width:20px;
  height:20px;
  padding:3px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border:1px solid #ccc;
  vertical-align:middle;
  background: #FBF9FF;
  background: -moz-linear-gradient(top, rgba(251,249,255,1) 0%, rgba(213,219,221,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#FBF9FF), color-stop(100%,#D5DBDD));
  background: -webkit-linear-gradient(top, #FBF9FF 0%,#D5DBDD 100%);
  background: -o-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -ms-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: linear-gradient(to bottom, #FBF9FF 0%,#D5DBDD 100%);
  /*filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbf9ff', endColorstr='#d5dbdd',GradientType=0 );*/
  margin-right:10px;
  border-radius: 50%;
}
.sv-radio-box.disabled{
  background: #D1D7D9;
}
.sv-radio-box>span{
  border-radius: 50%;
  position:absolute;
  top:50%;
  left:50%;
  background:none;
  display:inline-block;
  width:8px;
  height:8px;
  margin-left: -4px;
  margin-top:-4px;
  z-index:1;
}
.sv-radio-box.sv-checked>span{
  background:#000;
}
.sv-radio-box input{
  display:inline-block;
  position:absolute;
  width:100%;
  height:100%;
  opacity: 0;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  zoom: 1;
  cursor:pointer;
  margin:0;
  padding:0;
  top: 0;
  left: 0;
  z-index:2;
}
.sv-file-box{
  position:relative;
  overflow: hidden;
}
.sv-file-box{
  position:relative;
  overflow: hidden;
}
.sv-file-box.d-styles{
  display:inline-block;
  overflow:hidden;
  position:relative;
  font: normal 12px 'Arial',sans-serif;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  vertical-align:middle;
}

.sv-file-box.d-styles .sv-file-button{
  display:block;
  float:left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 10px;
  background: #FBF9FF;
  background: -moz-linear-gradient(top, rgba(251,249,255,1) 0%, rgba(213,219,221,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#FBF9FF), color-stop(100%,#D5DBDD));
  background: -webkit-linear-gradient(top, #FBF9FF 0%,#D5DBDD 100%);
  background: -o-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -ms-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: linear-gradient(to bottom, #FBF9FF 0%,#D5DBDD 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbf9ff', endColorstr='#d5dbdd',GradientType=0 );
  border-radius:4px 0 0 4px;
  border:1px solid #ccc;
}
.sv-file-box.d-styles.button-right .sv-file-button{
  float:right;
  border-radius:0 4px 4px 0;
}
.sv-file-box.d-styles .sv-file-field{
  display:block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 10px;
  background:#fff;
  border-radius:0 4px 4px 0;
  border:1px solid #ccc;
  border-left:0;
  box-shadow:inset 0px 2px 7px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.sv-file-box.d-styles.button-right .sv-file-field{
  border-radius:4px 0 0 4px;
  box-shadow:inset 2px 2px 7px rgba(0, 0, 0, 0.2);
}
.sv-file-box.d-styles.disabled .sv-file-button{
  background:#D1D7D9;
}
.sv-file-box.d-styles.disabled .sv-file-field{
  background:#D1D7D9;
}
.sv-file-box input{
  display:inline-block;
  position:absolute;
  width:100%;
  height:100%;
  opacity: 0;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  zoom: 1;
  font-size:600px;
  cursor:pointer;
  margin:0;
  padding:0;
  top: 0;
  left: 0;
  z-index:2;
}

.sv-select-box {
    height: 36px;
    position: relative;
    display:inline-block;
    font: normal 12px 'Arial',sans-serif;
}

.sv-select-box .sv-select-inner{
  display:inline-block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width:100%;
  height: 100%;
  background: #FBF9FF;
  background: -moz-linear-gradient(top, rgba(251,249,255,1) 0%, rgba(213,219,221,1) 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#FBF9FF), color-stop(100%,#D5DBDD));
  background: -webkit-linear-gradient(top, #FBF9FF 0%,#D5DBDD 100%);
  background: -o-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: -ms-linear-gradient(top, rgba(251,249,255,1) 0%,rgba(213,219,221,1) 100%);
  background: linear-gradient(to bottom, #FBF9FF 0%,#D5DBDD 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbf9ff', endColorstr='#d5dbdd',GradientType=0 );
  border-radius:4px;
  border:1px solid #ccc;
}
.sv-select-box.disabled .sv-select-inner{
  background: #D1D7D9;
}
.sv-select-box .sv-select-inner,.sv-select-box select{
  padding: 10px;
}
.sv-select-box .sv-select-inner{
  padding-right: 50px;
}
.sv-select-box .sv-select-button{
  position: absolute;
  width: 40px;
  height: 100%;
  top:0;
  right: 0;
  border-left: 1px solid #CCC;
}
.sv-select-box .sv-select-button .sv-select-icon{
  position:absolute;
  top: 50%;
  left: 50%;
  margin-top: -4px;
  margin-left: -6px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 6px 0 6px;
  border-color: #000 transparent transparent transparent;
}
.sv-select-box select{
  display:inline-block;
  position:absolute;
  width:100%;
  opacity: 0;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  zoom: 1;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  z-index:2;
  cursor: pointer;
}

