/* START lightslider */
@import '../plugins/lightslider/css/lightslider'; 
.lSAction.lSAction > a {
	width: 60px;
	height: 60px;
	margin-top: -30px;
	background-size: cover;
	
}

.lSAction.lSAction > .lSPrev {
	left: 0;
	background-image: url("../img/prev.png");
}

.lSAction.lSAction > .lSNext {
	right: 0;
	background-image: url("../img/next.png");
	background-position: 0 0;
}

/* END lightslider */


/* START jquery.sv.megabox.3.5.3 */
@import '../plugins/jquery.sv.megabox.3.5.3/style'; 
/* END jquery.sv.megabox.3.5.3 */

/* START jquery.sv.megastyler.1.3.1 */
@import '../plugins/jquery.sv.megastyler.1.3.1/style'; 
.sv-file-box {
	&:before {
		content: '';
		display: inline-block;
		width: 12px;
		height: 27px;
		margin-right: 10px;
		vertical-align: middle;
		background-image: url("../img/clip.png");
		background-size: cover;
	}
	.sv-file-field {
		display: inline-block;
		vertical-align: middle;
		border-bottom: 1px dashed #333;
		&:hover {
			border-bottom: 1px dashed transparent;
		}
	}
}
/* END jquery.sv.megastyler.1.3.1 */

/* START jquery.sv.megavalidator.3.3.3 */
@import '../plugins/jquery.sv.megavalidator.3.3.3/style'; 
/* END jquery.sv.megavalidator.3.3.3 */




/* START vars */

$minScreen: 320px;
$phonesScreen: 480px;
$tabletsPortraitScreen: 768px;
$tabletsLandscapeScreen: 992px;
$laptopsScreen: 1270px;

/* END vars */

/* START block */

.block_bigSlider {
	margin-top: 40px;
}

.block__title {
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: bold;
    text-transform: uppercase;
    line-height: 2;

}

.block_text {
	margin-top: 40px;
	margin-bottom: 40px;
	font-size: 16px;
}

.block_ps30 {
	@media screen and (min-width: $tabletsLandscapeScreen) {
		padding-left: 30px;
		padding-right: 30px;
	}

	.mapcontainer {
		margin-top: 40px;
	}
}

.block_text_framed {
	border: 1px solid #cacaca;
	padding: 30px;
	background-color: #FFF;

}

/* END block */




/* START bigSlider */
.bigSlider__item {
	position: relative;
	padding: 30px 50px;
	color: #fff;
	background-color: #2C4271;
	background-size: cover;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		padding: 40px 80px;
	}
	&:before {
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(44, 66, 113, 0.7);

	}
}

.bigSlider__content {
	position: relative;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		font-size: 18.75px;
		line-height: 1.6;
	}
}

.bigSlider__name {
	display: inline-block;
	padding: 5px 10px;
	border: 1px solid #fff;
	text-transform: uppercase;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		border: 3px solid #fff;
		font-size: 35px;
	}

}

.bigSlider__text {
	font-weight: 300;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		max-width: 65%;
	}
}

.bigSlider__name + .bigSlider__text {
	margin-top: 20px;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		margin-top: 50px;
	}
}


/* END bigSlider */



/* START got-icons */

.got-icons {
	background:rgb(44,66,113);
	padding-top:20px;
	padding-bottom:20px;
	margin-bottom:30px;
}
.icon-link img{
	position:relative;
	display:block;
	margin:0 auto;
	max-height:78px;
	overflow:hidden;
}
.icon-link {
	position:relative;
	display:block;
	padding:18px 10px 5px 10px;
}
.icon-name {
	position:relative;
	display:block;
	font-size:12px;
	line-height:17px;
	color:white;
	padding:0 10px;
	text-align:center;
	text-overflow:ellipsis;
	overflow: hidden;
}
.got-icon:hover .icon-name, .got-icon.active .icon-name {
	color:rgb(44,66,113);
}

.got-icon:hover, .got-icon.active {
	background-color:white;
}
@media screen and (min-width:979px) {
	.got-icon:hover, .got-icon.active  {
		background-color:transparent;
		background:url('../img/folder.png') no-repeat 0 0;
		background-size:cover;
	}
}
.got-icon .icon-link img.icon-a{
	display:none;
}
.got-icon:hover .icon-link img.icon, .got-icon.active .icon-link img.icon{
	display:none;
}
.got-icon:hover .icon-link img.icon-a, .got-icon.active .icon-link img.icon-a{
	display:block;
}
.got-icon {
	position:relative;
	width:10%;
	float:left;
	height:146px;
	margin-top:15px;
	margin-bottom:15px;
	text-overflow:ellipsis;
}
@media screen and (max-width:767px) {
	.got-icon {
		position:relative;
		width:16.666666666666666667%;
		float:left;
		height:90px;
	}
	.icon-name {
		display:none;
	}
}
@media screen and (max-width:479px) {
	.got-icon {
		position:relative;
		width:25%;
		float:left;
		height:90px;
	}
	.icon-name {
		display:none;
	}
}


/* END got-icons */


/* START brandsSlider */

.brandsSlider {
	text-align: center;
	list-style: none;
}

.brandsSlider__imgContainer {
	display: block;
	max-width: 270px;
	height: 100px;
	border: 1px solid #d6d6d6;
	font-size: 0;
	text-align: center;
	background-color: #FFF;
	overflow: hidden;
	&:before {
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;	
	}
}

.brandsSlider__imgContainer + .brandsSlider__imgContainer {
	margin-top: 40px;
}

.brandsSlider__img {
	max-width: 100%;
}

.brandsSlider__bottom {
	margin-top: 40px;
	text-align: center;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		margin-top: 60px;
	}

	.button {
		margin-right: 20px;
		margin-bottom: 20px;
		border: 0;
		@media screen and (min-width: $tabletsLandscapeScreen) {
			margin-right: 0;
		}
	}

	.button + .button {
		@media screen and (min-width: $tabletsLandscapeScreen) {
			margin-left: 70px;
		}		
	}
}



/* END brandsSlider */


/* awForm input */

.awForm {
	max-width: 90%;
	background-color: #e8e8e8;

	&.sv-megabox.default-style .sv-megabox-wrap {
		background-color: #e8e8e8;

	}	
	&.sv-megabox.default-style .sv-megabox-content {
		padding: 25px 30px; 
		@media screen and (min-width: $tabletsLandscapeScreen) {
			padding: 50px 60px; 
		}
		
	}

	.textarea {
		height: 190px;
	}
}

.awForm__header {
	p {
		line-height: 1.6;
	}
}

.awForm__inputName {
	margin-bottom: 10px;
	font-size: 16px;
}

.awForm__item + .awForm__item {
	margin-top: 22px;
}

.awForm__item_dsk_half {
	@media screen and (min-width: $tabletsLandscapeScreen) {
		display: inline-block;
		width: 45%;
		vertical-align: top;	
	}
}

.awForm__item_dsk_half + .awForm__item_dsk_half {
	@media screen and (min-width: $tabletsLandscapeScreen) {
		margin-left: 9%;
	}	
}

.awForm__footer {
	margin-top: 50px;
	text-align: center;
}

.awForm__item + .awForm__item_file {
	margin-top: 35px;
}

/* END awForm */


/* START textarea */

.textarea {
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 60px;
	border: 0;
	padding: 20px;
	font-size: 16px;
	background-color: #FFF;
	resize: none;
	border-radius: 6px;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		font-size: 20px;
	}	
}

/* END textarea */

/* START input */

.input {
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 60px;
	border: 0;
	padding-left: 20px;
	font-size: 16px;
	background-color: #FFF;
	border-radius: 6px;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		font-size: 20px;
	}
}

/* END input */

/* START button */

.button {
	position: relative;
	display: inline-block;
	border: 0;
	padding: 15px 35px;
    text-decoration: none;
    font-size: 17px;
    line-height: 25px;
    text-align: center;
    text-transform: uppercase;
    background: rgb(255,204,0);
    color: black;
    border-radius: 8px;
    @media screen and (min-width: $tabletsLandscapeScreen) {
    	padding: 15px 70px;
    }
    &:hover {
    	color: #000;
    	box-shadow: 3px 3px 7px 3px rgba(0, 0, 0, .9);	
    }
}

/* END button */


/* START importantText */

.importantText {
	border: 6px solid #ffcc00;
	padding: 20px 35px;
}

.importantText__title {
	margin-bottom: 20px;
	font-size: 20.8px;
	font-weight: 600;
	color: #2c4271;
}

/* END importantText */


/* START tab */

.tab__toggle {
  display: inline-block;
}

.tab__content {
  display: none;
} 

.tab__content_active {
  display: block;
}
/* END tab */


/* START projectSlider */

.projectSlider {
	position: relative;
	.lSAction.lSAction > .lSPrev {
		@media screen and (min-width: $laptopsScreen) {
			left: 275px;
		}
	}
}

.projectSlider__controllsInner {
	@media screen and (min-width: $laptopsScreen) {
		position: absolute;
		width: 275px;
		height: 100%;	
		background-color: rgba(56, 91, 166, 0.8);
		z-index: 1;
	}
}

.projectSlider__sliderItem {
	position: relative;
	z-index: 0;
	@media screen and (min-width: $tabletsLandscapeScreen) {
		text-align: right;
	}
}

.projectSlider__backgrounder {
	display: none;
	@media screen and (max-width: $tabletsLandscapeScreen - 1px) {
		background-image: none !important;
		
	}
	@media screen and (min-width: $tabletsLandscapeScreen) {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		background-size: cover;

	}
}

.projectSlider__tabsContainer {
	font-size: 0;
	background-color: #385ba6;
	background-color: rgba(56, 91, 166, 0.8);
	@media screen and (min-width: $laptopsScreen) {
		background-color: transparent;
	}

	
	.tab__toggle {
		display: inline-block;
		width: 50%;
		border: 0;
		padding: 10px 0;
		vertical-align: top;
		font-size: 18.75px;
		font-weight: 300;
		color: #fff;
		background-color: transparent;
		@media screen and (min-width: $tabletsLandscapeScreen) {
			padding: 15px 35px;
			padding-right: 0;
			text-align: left;
		}
		@media screen and (min-width: $laptopsScreen) {
			width: 100%;
		}
		
	}

	.tab__toggle_active {
		background-color: rgb(56, 91, 166);
	}
}

.projectSlider__contentContainer {
	position: relative;
	display: inline-block;
	padding: 20px 15px;
	font-size: 16px;
	text-align: left;
	z-index: 2;
	background-color: #e8e8e8;
	@media screen and (min-width: $phonesScreen) {
		padding: 20px 25px;
	}
	@media screen and (min-width: $tabletsLandscapeScreen) {
		width: 345px;
		padding: 25px 25px;
	}
	&:before {
		@media screen and (min-width: $laptopsScreen) {
			position: absolute;
			content: '';
			left: -70px;
			top: 0;
			width: 70px;
			height: 100%;
			background: linear-gradient(to left, #e8e8e8, transparent);
		}

	}

	p {
		font-size: 12.5px;
	}

	.scope_application {
		font-size: 11.25px;
		font-weight: 600;
	}

	.pokritie {
		font-size: 11.25px;
		font-weight: 600;		
	}

	.colors {
		margin-top: 10px;
		font-size: 0;
	}

	.text-colors {
		margin-bottom: 5px;
		font-size: 12.5px;
		font-weight: 600;
	}

	.color-wrap {
		position: relative;
		display: inline-block;
		width: 50%;
		margin-bottom: 15px;
		padding-left: 31px;
		vertical-align: top;
		font-size: 11.5px;
		text-transform: uppercase;
	}

	.icon-color {
		position: absolute;
		top: -3px;
		left: 0;
	}

	.button {
		@media screen and (min-width: $tabletsLandscapeScreen) {
			width: 100%;
			margin-top: 10px;
			padding-left: 0;
			padding-right: 0;

		}
	}
	.data {
		margin-top: 10px;
		margin-bottom: 15px;
	}
	.data__item {
		position: relative;
		font-size: 0;
		font-weight: 600;
		margin-bottom: 5px;
		&:after {
			position: absolute;
			content: '';
			bottom: 2px;
			left: 0;
			width: 100%;
			border-bottom: 1px dotted #000;
		}
	}
	.data__name {
		position: relative;
		display: inline-block;
		width: 50%;
		vertical-align: top;
		font-size: 11.25px;
		text-align: left;
		span {
			display: inline-block;
			background-color: #e8e8e8;
		}
	}

	.data__value {
		display: inline-block;
		width: 50%;
		vertical-align: top;
		font-size: 11.25px;
		text-align: right;
	}

}

.projectSlider__contentInner {
	position: relative;
}

.projectSlider__contentInner .tab__content {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    z-index: -1;
}

.projectSlider__contentInner .tab__content_active {
    position: static;
    opacity: 1;	
    z-index: 2;
}

.projectSlider__name {
	margin-bottom: 20px;
	font-size: 25px;
	font-weight: 600;
}

/* END projectSlider */


/* START mountSchemes */

.block_mountSchemes {
	
}

.mountSchemes__item {
	margin-bottom: 30px;
	border: 1px solid #cacaca;
	padding: 30px;
	background-color: #f7f7f7;
	&:after {
		content: '';
		display: block;
		clear: both;
	}
}

.mountSchemes__list_centered {
	text-align: center;
}

.mountSchemes__item_btn {
	text-align: center;	
}

.mountSchemes__outerLink {
	display: block;
	cursor: pointer;
	&:hover {
		.mountSchemes__imgContainer {
			border: 10px solid #2c4271;
		}

		.mountSchemes__name {
			color: #2c4271;
			text-decoration: underline;
		}
	}
}

.mountSchemes__imgContainer {
	margin-bottom: 15px;
	padding: 10px;
	border: 10px solid #fff;
	outline: 1px solid #cacaca;
	background-color: #FFF;
	transition: all 0.6s linear;
	img {
		display: block;
		width: 100%;
	}
}

.mountSchemes__name {
	min-height: 34px;
	font-size: 12.5px;
	text-transform: uppercase;
	text-align: center;
	color: #000;
	transition: all 0.6s linear;
}

/* END mountSchemes */